import React, { useState } from 'react';

const Search = ({ cb = Function.prototype }) => {
  const [value, setValue] = useState('');

  const handleSubmit = () => {
    cb(value);
  };

  const handleKey = (e) => {
    if ((e.key = 'Enter')) {
      handleSubmit();
    }
  };

  return (
    <div className="row">
      <div className="input-field col s12">
        <input
          type="search"
          id="search-field"
          placeholder="search"
          onKeyDown={handleKey}
          onChange={(e) => setValue(e.target.value)}
          value={value}
        />
        <button
          className="btn deep-purple lighten-2"
          style={{ position: 'absolute', top: 0, right: '10px' }}
          onClick={handleSubmit}
        >
          Search
        </button>
      </div>
    </div>
  );
};

export { Search };
